@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter.ttf");
  font-style: normal;
}
.footer {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 72px;
  background-color: rgb(255, 255, 255);
  border-top: 1px solid rgba(0, 0, 0, 0.16);
  padding: 0 24px;
  align-items: center;
}
@media (max-width: 1199px) {
  .footer {
    display: flex;
  }
}
@media (max-width: 767px) {
  .footer {
    height: 76px;
    padding: 0 16px;
  }
}
.footer__nav-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.footer-nav-element__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 4px;
  height: 40px;
  padding: 0 16px;
  border-radius: 99px;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #2f2227;
  transition: background-color 0.2s;
}
@media (max-width: 767px) {
  .footer-nav-element__link {
    height: 60px;
    flex-direction: column;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }
}
.footer-nav-element__link_active {
  background-color: rgba(0, 0, 0, 0.08);
}
@media (max-width: 767px) {
  .footer-nav-element__link_active {
    background-color: transparent;
    color: #d8598c;
  }
  .footer-nav-element__link_active .footer-nav-element {
    color: #d8598c;
  }
}
.footer-nav-element__link:hover:not(.footer-nav-element__link_active) {
  background-color: #f7dee8;
}
.footer-nav-element__link:active:not(.footer-nav-element__link_active) {
  background-color: #d8598c;
  color: #fff;
}
.footer-nav-element__link:active:not(.footer-nav-element__link_active) .footer-nav-element__icon {
  color: #fff;
}
.footer-nav-element__icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  height: 80px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 24px;
}
@media (max-width: 767px) {
  .header__content {
    padding: 0 16px;
  }
}
.header__logo {
  width: 241px;
  height: 40px;
}
.header__logo img {
  width: 100%;
}
.header__button {
  flex-shrink: 0;
  width: 48px;
  height: 48px;
}
.header__button-icon {
  width: 32px;
  height: 32px;
}

.nav {
  display: flex;
  margin-left: 64px;
  margin-right: auto;
}
@media (max-width: 1199px) {
  .nav {
    display: none;
  }
}
.nav__list {
  display: flex;
  flex-wrap: nowrap;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 12px;
}

.nav-element__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 4px;
  height: 40px;
  padding: 0 16px;
  border-radius: 99px;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #2f2227;
  transition: background-color 0.2s;
}
.nav-element__link_active {
  background-color: rgba(0, 0, 0, 0.08);
}
.nav-element__link:hover:not(.nav-element__link_active) {
  background-color: #f7dee8;
}
.nav-element__link:active:not(.nav-element__link_active) {
  background-color: #d8598c;
  color: #fff;
}
.nav-element__link:active:not(.nav-element__link_active) .nav-element__icon {
  color: #fff;
}
.nav-element__icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  color: rgba(47, 34, 39, 0.8);
}

.select-component-wrapper {
  position: relative;
}
.select-component-wrapper .select-component {
  position: relative;
  min-width: 124px;
  padding: 0 12px;
  height: 44px;
  display: flex;
  align-items: center;
  border-radius: 99px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  transition: border-color 0.2s;
}
.select-component-wrapper .select-component:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.select-component-wrapper .select-component:focus {
  outline: none;
  box-shadow: none;
}
.select-component-wrapper .select-component_selected {
  border-bottom-color: #2f2227;
  background-color: #2f2227;
}
.select-component-wrapper .select-component_selected .ss-values {
  color: #fff !important;
}
.select-component-wrapper .select-component_selected ~ .select-component__reset {
  color: rgba(255, 255, 255, 0.72);
}
.select-component-wrapper .select-component.ss-disabled {
  border-color: transparent;
  background-color: rgba(0, 0, 0, 0.04);
}
.select-component-wrapper .select-component.ss-disabled .ss-values {
  color: rgba(47, 34, 39, 0.8);
}
.select-component-wrapper .select-component.ss-disabled .ss-arrow path {
  fill: rgba(47, 34, 39, 0.8);
}
.select-component-wrapper .select-component__label {
  margin-bottom: 4px;
  color: rgba(47, 34, 39, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component__reset {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 34px;
  top: 5px;
  right: 12px;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.select-component-wrapper .select-component__reset-icon {
  width: 16px;
  height: 16px;
  fill: #8f8f8f;
}
.select-component-wrapper .select-component .ss-values {
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component .ss-values .ss-placeholder,
.select-component-wrapper .select-component .ss-values .ss-single {
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 24px;
}
.select-component-wrapper .ss-open-below {
  border-color: #66a6d4;
  border-radius: 22px 22px 0 0;
}
.select-component-wrapper .ss-open-below:hover {
  border-color: #66a6d4;
}
.select-component-wrapper .ss-arrow {
  flex-shrink: 0;
  width: 12px;
  height: 12px;
  margin-left: 12px;
  margin-right: 4px;
}
.select-component-wrapper .ss-arrow path {
  stroke: none;
  fill: #2f2227;
}

.ss-content {
  margin-top: 0 !important;
  border-radius: 2px;
  border-bottom-right-radius: 2px !important;
  border-bottom-left-radius: 2px !important;
  background: rgb(255, 255, 255);
  box-shadow: 0px 16px 24px 0px rgba(0, 0, 0, 0.06), 0px 2px 6px 0px rgba(0, 0, 0, 0.04), 0px 0px 1px 0px rgba(0, 0, 0, 0.04);
}
.ss-content .ss-option:not(.ss-hide) {
  display: block !important;
  border-radius: 2px;
  padding: 14px 16px !important;
  color: #2f2227 !important;
  font-size: 14px;
  line-height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ss-content .ss-option:not(.ss-hide):hover {
  background-color: #e6f0f8 !important;
}
.ss-content .ss-option:not(.ss-hide).ss-selected {
  background-color: #e6f0f8 !important;
}
.ss-content .ss-list::-webkit-scrollbar {
  width: 4px;
}
.ss-content .ss-list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border: 2px solid rgba(0, 0, 0, 0.16);
}
.ss-content .ss-list::-webkit-scrollbar-track {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.pagination {
  display: flex;
  flex-wrap: nowrap;
  gap: 4px;
  margin-left: auto;
  margin-right: auto;
}
.pagination__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  border-radius: 50%;
  border: 2px solid transparent;
  background-color: transparent;
  width: 48px;
  height: 48px;
  cursor: pointer;
  color: #005693;
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  transition: all 0.2s;
}
.pagination__button:hover:not(.pagination__button_disabled, .pagination__button_active) {
  border-color: #006bb8;
}
.pagination__button_active {
  color: #2f2227;
  background-color: rgba(0, 0, 0, 0.08);
}
.pagination__button_disabled {
  opacity: 0.48;
  cursor: default;
}
.pagination__icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}
.pagination__icon_right {
  transform: rotate(180deg);
}
@media (max-width: 767px) {
  .pagination__button_desktop {
    display: none;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 30px;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 99px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.primary {
  border-color: #d8598c;
  background-color: #d8598c;
  color: #fff;
}
.button.primary:hover:not(:disabled) {
  border-color: #ad4770;
  background-color: #ad4770;
}
.button.primary:active:not(:disabled) {
  border-color: #823554;
  background-color: #823554;
}
.button.primary:disabled {
  opacity: 0.48;
}
.button.secondary {
  background-color: rgb(255, 255, 255);
  border-color: rgba(47, 34, 39, 0.8);
  color: #2f2227;
}
.button.secondary:hover:not(:disabled) {
  border-color: #d8598c;
  background-color: #f7dee8;
}
.button.secondary:active:not(:disabled) {
  border-color: #823554;
  background-color: #823554;
  color: #fff;
}
.button.secondary:disabled {
  opacity: 0.48;
}

.button-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 99px;
  background-color: transparent;
  color: #2f2227;
  cursor: pointer;
  transition: all 0.2s;
}
.button-icon:focus {
  outline: none;
}
.button-icon:hover {
  background-color: #f7dee8;
}
.button-icon:active {
  background-color: #d8598c;
  color: #fff;
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__label {
  margin-bottom: 4px;
  margin-left: 2px;
  font-size: 14px;
  line-height: 20px;
  color: rgba(47, 34, 39, 0.8);
}
.text-input__input {
  min-height: 48px;
  padding: 10px 34px 10px 16px;
  border-radius: 4px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background-color: rgb(255, 255, 255);
  color: inherit;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgba(47, 34, 39, 0.56);
}
.text-input__input:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.text-input__input:focus {
  outline: none;
  border-color: #66a6d4;
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-input__input.error {
  border-color: #ff5459;
}
.text-input__input_force-alert {
  border-color: #ff5459;
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
}
.text-input__reset.active {
  display: flex;
}
.text-input__show {
  display: flex;
}
.text-input__show.shift {
  right: 40px;
}
.text-input__reset, .text-input__show {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 12px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset:hover svg, .text-input__show:hover svg {
  color: #2f2227;
}
.text-input__reset.active, .text-input__show.active {
  display: flex;
}
.text-input__icon {
  color: #2f2227;
  transition: all 0.2s;
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  margin-left: 2px;
  color: #ff5459;
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__addon {
  margin-top: 4px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__sub-text {
  margin-top: 4px;
  margin-left: 2px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  margin-left: 2px;
  color: #ff5459;
  font-size: 14px;
  line-height: 20px;
}

.card-id {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 274px;
  padding: 24px;
  border-radius: 24px;
  background-color: rgba(0, 0, 0, 0.04);
}
@media (max-width: 767px) {
  .card-id {
    width: 100%;
  }
}
.card-id__block {
  display: flex;
  gap: 8px;
}
.card-id__title-block {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding-bottom: 8px;
}
.card-id__title {
  font-size: 14px;
  line-height: 20px;
  color: rgba(47, 34, 39, 0.8);
}
.card-id__number {
  font-size: 16px;
  line-height: 24px;
}
.card-id__copy-block {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: auto;
}
.card-id__copy {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
}
.card-id__copy-icon {
  width: 24px;
  height: 24px;
}

.badge {
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  padding: 2px 8px 2px 2px;
  border-radius: 99px;
  font-size: 14px;
  line-height: 20px;
}
@media (max-width: 767px) {
  .badge {
    padding: 2px 8px;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }
}
.badge::before {
  content: "";
  display: flex;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .badge::before {
    display: none;
  }
}
.badge_green {
  background-color: #d4f1de;
  color: #1b823f;
}
.badge_green::before {
  background-image: url("../img/plus.svg");
}
.badge_blue {
  background-color: #cce1f1;
  color: #005693;
}
.badge_blue::before {
  background-image: url("../img/check.svg");
}
.badge_red {
  background-color: #ffddde;
  color: #cc4347;
}
.badge_red::before {
  background-image: url("../img/fire.svg");
}

.content-index {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.transfer {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.transfer__title {
  display: flex;
  flex-direction: column;
  gap: 4px;
  font-size: 24px;
  font-weight: 900;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.transfer__title-link {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #005693;
  text-decoration: none;
}
.transfer__cards {
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .transfer__cards {
    flex-direction: column;
    gap: 16px;
  }
}

.transfer-card {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 149px;
}
@media (max-width: 767px) {
  .transfer-card {
    width: 100%;
  }
}
.transfer-card__balance {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  gap: 2px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.transfer-card__balance::after {
  flex-shrink: 0;
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-image: url("../img/coin.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.filters {
  display: flex;
  gap: 4px;
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.filters::-webkit-scrollbar {
  display: none;
}

.table-history {
  display: grid;
  grid-template-columns: 128fr 160fr 240fr 184fr 440fr;
}
@media (max-width: 1199px) {
  .table-history {
    grid-template-columns: 128fr 160fr 128fr 144fr 160fr;
  }
}
@media (max-width: 767px) {
  .table-history {
    grid-template-columns: 1fr;
  }
}
.table-history__head {
  display: contents;
}
.table-history__head .table-cell-h:nth-child(1) {
  padding-left: 0;
}
@media (max-width: 767px) {
  .table-history__head {
    display: none;
    visibility: hidden;
  }
}
.table-history__cell-h {
  display: flex;
  align-items: center;
  padding: 16px 8px 14px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.16);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.table-history__cell-h_right {
  justify-content: flex-end;
}
.table-history__row {
  display: contents;
  padding: 16px 0;
}
@media (max-width: 767px) {
  .table-history__row {
    display: grid;
    gap: 4px;
    grid-template-columns: 82fr 146fr 100fr;
    grid-template-areas: "partner partner sum" "type date date" "description description description";
    border-bottom: 1px solid rgba(0, 0, 0, 0.16);
  }
}
.table-history__cell {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid rgba(0, 0, 0, 0.16);
  padding: 24px 8px 23px;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 767px) {
  .table-history__cell {
    border-bottom: none;
    padding: 0;
  }
}
.table-history__cell_right {
  justify-content: flex-end;
}
.table-history__cell_compact {
  padding: 16px 8px 15px;
}
@media (max-width: 767px) {
  .table-history__cell_compact {
    padding: 0;
  }
}
.table-history__cell:nth-child(1) {
  padding-left: 0;
}
@media (max-width: 767px) {
  .table-history__cell:nth-child(1) {
    grid-area: type;
    margin-top: 4px;
  }
}
@media (max-width: 767px) {
  .table-history__cell:nth-child(2) {
    grid-area: date;
    margin-top: 4px;
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .table-history__cell:nth-child(3) {
    grid-area: partner;
  }
}
@media (max-width: 767px) {
  .table-history__cell:nth-child(4) {
    grid-area: sum;
  }
}
@media (max-width: 767px) {
  .table-history__cell:nth-child(5) {
    grid-area: description;
    font-size: 14px;
    line-height: 20px;
  }
}
.table-history__partner {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  gap: 8px;
}
.table-history__partner img {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
}
.table-history__sum {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  gap: 2px;
}
.table-history__sum::after {
  content: "";
  display: flex;
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url("../img/coin.svg");
}
.table-history__sum_red {
  color: #cc4347;
}
.table-history__sum_red::after {
  background-image: url("../img/coin-red.svg");
}
.table-history__text {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.table-history__text-sub {
  font-size: 14px;
  line-height: 20px;
  color: rgba(47, 34, 39, 0.8);
}
@media (max-width: 767px) {
  .table-history__text-sub {
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }
}

.content-profile {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.profile {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.profile-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}
@media (max-width: 767px) {
  .profile-cards {
    display: flex;
    flex-direction: column;
  }
}

.profile-card {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 16px 16px 24px;
  border-radius: 24px;
  border: 1px dashed #006bb8;
  background-color: rgb(255, 255, 255);
}
.profile-card_selected {
  border: 1px solid rgba(0, 0, 0, 0.16);
}
.profile-card__block {
  display: flex;
  flex-direction: column;
}
.profile-card__block_content {
  gap: 2px;
  padding-bottom: 16px;
}
.profile-card__img {
  width: 64px;
  height: 64px;
  object-fit: contain;
}
.profile-card__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.2px;
}
.profile-card__text {
  font-size: 14px;
  line-height: 20px;
}
.profile-card__link {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #006bb8;
  text-decoration: none;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 360px;
  width: 100%;
}
.form__controls {
  display: flex;
  gap: 8px;
  padding-top: 16px;
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  overflow-x: hidden;
  font-size: 20px;
  line-height: 1.6;
  font-weight: 400;
  font-family: "Inter", "Helvetica", sans-serif;
  color: #2f2227;
  background-color: rgb(255, 255, 255);
}
@media (max-width: 767px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}

@media (max-width: 767px) {
  .mobile-none {
    display: none;
  }
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 1200px;
  width: 100%;
  margin: 80px auto 0;
  padding: 24px;
}
@media (max-width: 1199px) {
  .content {
    margin-bottom: 72px;
  }
}
@media (max-width: 767px) {
  .content {
    padding: 24px 16px;
    margin-bottom: 76px;
  }
}
.content_index {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 32px;
}
@media (max-width: 1199px) {
  .content_index {
    display: flex;
    flex-direction: column-reverse;
  }
}
.content_profile {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 32px;
}
@media (max-width: 1199px) {
  .content_profile {
    display: flex;
    flex-direction: column;
  }
}

.content-title {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.content-title__title-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.content-title__title {
  display: flex;
  gap: 8px;
  margin: 0;
  font-size: 24px;
  font-weight: 900;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.content-title__count {
  font-weight: 500;
  color: rgba(47, 34, 39, 0.56);
}
.content-title__balance {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 96px;
  line-height: 112px;
  letter-spacing: -5.76px;
}
@media (max-width: 767px) {
  .content-title__balance {
    gap: 4px;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: -1.92px;
  }
}
.content-title__icon {
  flex-shrink: 0;
  width: 84px;
  height: 84px;
}
@media (max-width: 767px) {
  .content-title__icon {
    width: 42px;
    height: 42px;
  }
}